import React from 'react';
import './footer.css';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import phone from '../../images/icons/phone.svg';
import location from '../../images/icons/location.svg';
import email from '../../images/icons/email.svg';
import fb from '../../images/icons/fb.svg';
import twitter from '../../images/icons/twitter.svg';
import instagram from '../../images/icons/instagram.svg';
import tg from '../../images/icons/tg.svg';

export default function Footer() {
	return (
		<div className="footer">
			<Container>
				<Row>
					<Col xs={12} sm={6} md={4}>
						<div className="footer-header">About</div>
						<div className="footer-list">
							<ul>
								<li>About Us</li>
								<li>Our Projects</li>
								<li>Privacy Policy</li>
								<li>Join us</li>
							</ul>
						</div>
					</Col>
					<Col xs={12} sm={6} md={4}>
						<div className="get-in-touch">
							<div className="footer-header">Get In Touch</div>
							<ul className="contacts">
								<li>
									<img src={phone} className="icon" />
									<span>+99890 999 69 96</span>
								</li>
								<li>
									<img src={location} className="icon" />
									<span>9, Ziyolilar str., M.Ulugbek district, Tashkent city, 100170</span>
								</li>
								<li>
									<img src={email} className="icon" />
									<span>info@raisense.uz</span>
								</li>
							</ul>
						</div>
						<div className="social-list footer-social">
							<a className="social-item" href="https://www.facebook.com/raisense.uz/">
								<img src={fb} className="fb" />
							</a>
							<a className="social-item" href="https://www.instagram.com/raisense.uz/">
								<img src={twitter} />
							</a>
							<a className="social-item" href="https://t.me/raisense">
								<img src={instagram} />
							</a>
							<a className="social-item" href="#">
								<img src={tg} />
							</a>
						</div>
					</Col>
					<Col xs={12} sm={6} md={4}>
						<div className="map">
							<div className="footer-header">Location</div>
							<iframe
								src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d47930.17816276397!2d69.33453!3d41.338525!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x3772abeffc72e7b8!2z0KPQvdC40LLQtdGA0YHQuNGC0LXRgiDQmNC90YXQsA!5e0!3m2!1sru!2sus!4v1574080152946!5m2!1sru!2sus"
								frameBorder="0"
								width="100%"
								height="100%"
								style={{ border: '0' }}
								allowFullScreen=""
							/>
						</div>
					</Col>
				</Row>

				<div className="copyright">
					{' '}
					© {new Date().getFullYear()},
					{` `}
					All Right Reserved 2019, Raisense.
				</div>
			</Container>
		</div>
	);
}
